@import url("https://fonts.googleapis.com/css?family=Inter:300,400,500,600,700|Montserrat:300,400,500,600,700|Nunito+Sans:300,400,500,600,700|Poppins:300,400,500,600,700|Roboto:300,400,500,600,700");

::-webkit-scrollbar {
  width: 2px;
  background: transparent;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  min-height: 100vh;
  background: linear-gradient(180deg, #f0f2fd 0%, #fff8f9 100%);
}
